@charset "utf-8";

.c-play-button {
    position: relative;
    display: block;
    padding: 10px 15px 10px 30px;
    border-radius: 5px;
    border: solid 1px $color_main;
    // background-color: $color-main;

    &:hover {
        background-color: $color-main;
        text-decoration: none;
    }

    &::after {
        display: block;
        content: '▶︎';
        position: absolute;
        left: 12px;
        top: 12px;
        font-size: $font_xs;

    }
    
    @include mq(){

    }

}